
































































































































































































































































































































.hr {
    width: 100%;
    height: 1px;
    background: #f5f5f6;
}
.body2_form {
    margin: 20px 0;
}
.Remarks_box {
    .table_titel {
        padding: 20px 0;
        display: flex;
        justify-content: space-between;
        span {
            font-size: 16px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #333333;
        }
    }
}
.body2 {
    .body2_form_img {
        width: 100px;
        height: 100px;
        background: #ccc;
    }
    .body2_form_box {
        display: flex;
    }
    .body2_form_txt {
        max-width: 310px;
        margin-left: 10px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        line-height: 25px;
        .body2_form_title {
            font-size: 16px;
            color: #333333;
        }
        .body2_form_price {
            font-size: 14px;
            color: #333333;
        }
        .body2_form_number {
            font-size: 14px;
            color: #999999;
        }
    }
}
.body2_box {
    margin: 20px 0;
    display: flex;

    .body2_itme {
        width: 25%;
        display: flex;
        align-items: center;
        .body2_itme_img {
            width: 56px;
            height: 56px;
            background: #ccc;
        }
        .body2_itme_txt {
            padding-left: 10px;
            line-height: 33px;
            p {
                font-size: 14px;
                font-family: PingFangSC-Medium, PingFang SC;
                font-weight: 500;
                color: #333333;
            }
            span {
                font-size: 14px;
                font-family: PingFangSC-Regular, PingFang SC;
                font-weight: 400;
                color: #666666;
            }
        }
    }
}
.body1 {
    padding: 22px;
    box-sizing: border-box;
    display: flex;
    height: 97px;
    border: 1px solid #f5f5f6;
    .body1_icon {
        display: inline-block;
        width: 22px;
        height: 22px;
        text-align: center;
        line-height: 22px;
        border-radius: 50%;
        background: #25cf5d;
        span {
            color: #fff;
        }
    }
    span {
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #333333;
    }
    .body1_p {
        margin-left: 40px;
        margin-top: 14px;
        p {
            font-size: 14px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #666666;
        }
    }
}
.body2 {
    padding: 20px;
    margin-top: 20px;
    border: 1px solid #f5f5f6;
    .p {
        p {
            font-size: 16px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: #333333;
        }
    }
}
